        // 
        this.hfwid = wid;
        this.$get(this.API.canDealSuspend, {
          hfwid: wid,
          isagree: data
        }).then(res => {
          if (res.state == 'success') {
            if (!!res.data) {
              // 
              if (data != 1) {
                this.$post(this.API.dealSuspend, {
                  hfwid: wid,
                  isagree: data
                }).then(res => {
                  if (res.state == 'success') {
                    this.$message({
                      message: '',
                      type: 'success'
                    });
                    if (data != 2) {
                      this.queryQuestion(this.wid);
                    }
                    this.queryAnswers(this.wid);
                  } else {
                    this.$message({
                      message: res.msg,
                      type: 'error'
                    });
                  }
                });
              } else {
                // 
                this.dialogVisibleGq = true;
                this.suspendType = 'bh';
              }
            } else {
              this.$message({
                message: '',
                type: 'warning'
              });
            }
          } else {
            this.$message({
              message: res.msg,
              type: 'error'
            });
          }
        });
